import gsap from "gsap";

export default {
    layout: 'TestLayout',
    components: {
        
    },
    data() {
        return{
            loading: 0,
            dialog: false,
            beforeSelect: true,
            req: 0,
            grant: 2,
            step: 1,
        }
    },
    computed: {
        templist() {
            return this.$LANGUAGES_KO;
        },
        req_lang() {
            return this.templist[this.req];
        },
        grant_lang() {
            return this.templist[this.grant]
        }
    },
    watch: {
        /*loading(value) {
            if (value >= 100) {
                this.beforeSelect = true;
            }
            if (value >= 30 && value < 60) {
                this.step = 2;
            }
            if (value >= 60 && value < 100) {
                this.step = 3;
            }
        }*/
    },
    methods: {
        loadingLogic(step) {
            switch(step) {
                case 1:
                    // 영상 업로드
                    this.timerOn();
                    break;
                case 2:
                    // 영상 인식
                    this.timerOn();
                    break;
                case 3:
                    // 자막 번역
                    this.timerOn();
                    break;
                default:
                    // 종료 트리거를 받는다면
                    this.loading = this.step * 33;
                    this.step++;
            }
        },
        timerOn() {
            this.beforeSelect = false;
            const interval = setInterval(() => {
                if (this.loading < this.step*33 && this.dialog === true) {
                    this.loading += 1;
                } else {
                    clearInterval(interval);
                }
            }, 100);
        },
        async triggerStart() {
            this.loadingLogic(this.step);
            setTimeout(() => {
                console.log("step 넘어감");
                this.loadingLogic("넘겨");
                this.loadingLogic(this.step);
            }, 1000);
            setTimeout(() => {
                console.log("step 넘어감");
                this.loadingLogic("넘겨");
                this.loadingLogic(this.step);
            }, 2000);
            setTimeout(() => {
                this.loading = 100;
            }, 3000);
        },
        close() {
            this.dialog = false;
            this.loading = 0;
        },
    },
}
